import React from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import seo from '../images/seo.png';
import googleads from '../images/googleAds.png';
import copywriting from '../images/copywriting.png';
import reactimage from '../images/reactimage.jpg';
import { Link } from 'react-router-dom';

export const Certifications = () => {
    const certy = [
        {
          name:"ReactJs",
          url:"https://www.udemy.com/share/101vXK3@FMtsP3iqcUiw5q1pUbXNnaKKGJLXv_Eyxe2ZDVGYM1XFrKVEHJqAo1WBEJdpe2RHRg==/",
          image:reactimage,
          desc:"This is a certification that taught me about how to make single page applications using ReactJs along with basics of NodeJs, Express.Js, and MongoDb."
        },
        {
            name:"SEO",
            url:"http://ude.my/UC-706a0021-e232-48e0-bad4-efe3d76c565b",
            image:seo,
            desc:"This is a certification that taught me about how to make a web page at the top results in google search."
        },
        {
            name:"Copywriting",
            url:"http://ude.my/UC-b2d93cee-0913-4efb-bd9b-64c5e5c7a36c",
            image:copywriting,
             desc:"This is a certification that taught me about how to write for a product so that its sales can be increased."
        },
        {
            name:"Google Ads",
            url:"http://ude.my/UC-1974ecf7-72e8-4414-b650-e63af64f4fb0",
            image:googleads,
              desc:"This is a certification that taught me about how to make a ads for a product so that its sales can be increased."
        }
       
    ]
  return (
    <div className='certification bona-nova-sc-bold'>
        <h1 className='heading bona-nova-sc-bold'>
          My Certification:
        </h1>
        <div className='certy'>
            {
                certy.map((item,idx)=>(
                    <Card className='certy_card '>
      <Card.Img variant="top" src={item.image}  className='certy_images'/>
      <Card.Body>
        <Card.Title>{item.name}</Card.Title>
        <Card.Text>
         {item.desc}
        </Card.Text>
        <Link to= {item.url} className='link'>See Certificate</Link>
      </Card.Body>
    </Card>
                ))
            }

        </div>
    </div>
  )
}
