import React from 'react';
import Card from 'react-bootstrap/Card';

export const Education = () => {
  return (
    <div className='container education bona-nova-sc-bold '>
        <h1 className='heading bona-nova-sc-bold' >
            My Education
        </h1>
        <div className='education_list'>
        <Card>
      <Card.Header as="h5">Graduation</Card.Header>
      <Card.Body>
        <Card.Title>Seth Jai Prakash Mukand Lal Institute of Engineering and Technology.</Card.Title>
        <hr/>
        <Card.Text>
         I graduated with a Bachelor's of Computer Application with overall percentage of 77.2%. This is a college located at Radaur,(Yamunanagar), Haryana.
         I have studied subjects like:<br/> 
         1. Data Structures<br/> 
         2. Database Management System.<br/> 
         3. Object Oriented Programming.
        </Card.Text>
      </Card.Body>
    </Card>
    <Card>
      <Card.Header as="h5">Higher Education</Card.Header>
      <Card.Body>
        <Card.Title>Indian Public Sr. Sec. School,Radaur, Yamunanagar (Haryana).</Card.Title>
        <hr/>
        <Card.Text>
         I have  done  my  higher  education in Sciences(Non-Medical). There I have studied subjects like:<br/> 
         1. Physics<br/> 
         2. Chemistry.<br/> 
         3. Maths.
        </Card.Text>
      </Card.Body>
    </Card>
    <Card>
      <Card.Header as="h5">Metriculation</Card.Header>
      <Card.Body>
        <Card.Title>Indian Public Sr. Sec. School,Radaur, Yamunanagar (Haryana).</Card.Title>
        <hr/>
        <Card.Text>
         I have  done  my merticulation education in Sciences with 95%. There I have studied subjects like:<br/> 
         1. Physics<br/> 
         2. Chemistry.<br/> 
         3. Maths.<br/>
         4. Social Sciences
        </Card.Text>
      </Card.Body>
    </Card>

        </div>
    </div>
  )
}
