import React from 'react';
import ankit from '../images/MyPhotoes7-Photoroom.jpg';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Image from 'react-bootstrap/Image';
import Row from 'react-bootstrap/Row';
import { Services } from './services';
import { HomeProjects } from './homeProjects';
import { Education } from './education';
import { Certifications } from './certifications';
import {motion} from 'framer-motion';

export const Home = () => {
  return (
    <motion.div
    initial={{width:0,opacity:1}}
    animate={{width:"100%",opacity:1}}
    exit={{x:window.innerWidth,transition:{duration:0.05}}}
    >
    <div className='banner'>
      <div className='banner_image_div'>
    <Container className='imageContainer'>
      <Row>
      
        <Col xs={6} md={4}>
          <Image src={ankit} roundedCircle className='My_image' />
        </Col>
     
      </Row>
    </Container>
    <div className='name bona-nova-sc-bold '>
      <h1 className='bona-nova-sc-bold'>
        Ankit Gupta
      </h1>
      <h1>
        ReactJs Developer
      </h1>
      <p>
       I am a process-oriented ReactJs Developer who has built significant projects.
       <br></br>
       I also have knowledge in SEO, PHP, and Copywriting.  
      </p>
    </div>

    
    </div>
    {/* /////////////////////////////////////////////////// */}
  
    
     
    </div>
    <div className='banner_2'>
    <Services/>
    <HomeProjects/>
    <Education/>
    <Certifications/>

    </div>
  
    </motion.div>
  )
}
