import React from 'react';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';

import simpleNotes from '../images/desktopNotes.png';
import flickbase from '../images/FlickBase.png';

export const HomeProjects = () => {
  return (
    <div className='bona-nova-sc-bold'>
        <hr></hr>
        <h1 className='heading bona-nova-sc-bold'>
            Projects
        </h1>
        <hr></hr>
        <div className='projectContainer'>
        <Card className='project_card' >
      <Card.Img variant="top" src={simpleNotes} className='card_image' />
      <Card.Body className='card_body'>
        <Card.Title> <strong style={{fontSize:"40px"}}>Simple Notes</strong> </Card.Title>
        <Card.Text>
         <strong style={{fontSize:"20px"}}> SimPLE Notes</strong> is a web application that helps the user to take notes. It is an application that provides many features such as Private section for notes, Add to favourites, etc.
        </Card.Text>
        <Link to="/projects" className='link'>Know More</Link>
      </Card.Body>
    </Card>
    <Card className='project_card flickbase' >
      <Card.Img variant="top" src={flickbase} className='card_image' />
      <Card.Body className='card_body'>
        <Card.Title> <strong style={{fontSize:"40px"}}>FlickBase</strong> </Card.Title>
        <Card.Text>
         <strong style={{fontSize:"20px"}}> FlickBase</strong> is a web application that helps the user know the details of the latest released movies and web series.It is an application that have features like seperate admin panel and user panel.
        </Card.Text>
        <Link to="/projects" className='link'>Know More</Link>
      </Card.Body>
    </Card>


        </div>
    </div>
  )
}
