import React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { LinkContainer } from 'react-router-bootstrap';

function Header() {
  return (
    <div>
      <Navbar  data-bs-theme="dark" className='navbar' >
        <Container className='header_container' style={{width:"100%",margin:"0 0"}} >
        <Navbar.Brand  className='navbrand bona-nova-sc-regular'>Portfolio</Navbar.Brand>
          <Nav className="me-autov"   style={{display:"flex",gap:"20px",width:"80%",margin:"auto",justifyContent:"center",alignContent:"center"}}>
           
           
            

          
            <LinkContainer to="/">
            <Nav.Link className='navLink bona-nova-sc-regular'>Home</Nav.Link>

            </LinkContainer>
            <LinkContainer to="/projects">
            <Nav.Link className='navLink bona-nova-sc-regular'>Projects</Nav.Link>

            </LinkContainer>
            <LinkContainer to="/beyond">
            <Nav.Link className='navLink bona-nova-sc-regular'>Beyond</Nav.Link>

            </LinkContainer>
          </Nav>
        </Container>
      </Navbar>
    
    </div>
  );
}

export default Header;
