import "./App.css";
import {BrowserRouter} from "react-router-dom";
import Header from "./components/header";
import { Footer } from "./components/footer";
import { AnimateRoutes } from "./components/animateRoutes";

function App() {
  return (
   <>
    <BrowserRouter>
    <Header/>
    <AnimateRoutes/>
   
    <Footer/> 
    </BrowserRouter>
 
   </>
  );
}

export default App;
